body
{
    overflow-x: hidden;
    background:#000;
    color: rgb(180, 180, 180);
}

.SearchApp
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.serachinput
{
    outline: none;
    border-radius: 7px;
    padding: 15px 30px;
    background:#000;
    color: #59616b;
    margin-bottom: 10px;
}

.user
{
    width: 100%;
    height: auto;
    margin-bottom: 5px;
    border: 1px solid #636e72;
    border-radius: 5px;
    padding-left: 5px;
}